.App {
  background-color: #222;
  -ms-flex: 1 1;
      flex: 1 1;
  min-height: 100%;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  display: inline;
  height: 60px;
  padding-left: 10px;
  vertical-align:middle;
}

.App-header {
  background-color: #222;
  color: white;
  height: 60px;
  padding-bottom: 20px;
  padding-top: 10px;
  text-align: left;
}

ul.header{
  display: inline;
  padding: 0
}

ul.header li{
  display: inline;
  list-style-type: none;
}

ul.header li a{
  color: #FFF;
  font-weight: bold;
  padding: 20px;
  text-decoration: none;
}

.active{
  background-color: #1DC85B
}

.App-title {
  display: inline;
  font-size: 1.5em;
  padding: 10px;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.content{
    background-color: #222;
    min-height:100%;
    position:relative;
}

.gifbox{
  padding-left: 20px;
  padding-right: 20px
}
.gif{
  display: inline;
  margin-left: auto;
  margin-right: auto
}

.App-footer{
  background-color: #222;
  bottom: 0;
  color: white;
  margin: auto auto 0 auto;
  text-align: right;
  position: fixed;
  width: 100%
}

.App-footer p{
  padding-right: 15px
}
.App-footer a{
  color: #FFF;
}
.foot{
  vertical-align:middle;
}

.txtbox
    {
     font-size:15pt;
     height:30px;
     width:400px;
     bottom:20px;
     background-color: transparent;
     padding: 4px;
     vertical-align: middle;
     color:white ;
    }

.cafesize{
  height:72px;
  width:54px;
  margin-left: 8px;
  bottom:0px;
  vertical-align: middle;

}



